 .App {
  height: 100vh;;
}


 .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}

 .trigger:hover {
  color: #1890ff;
}

 .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.3);
  margin: 16px;
}

.site-layout .site-layout-background {
  background: #fff;
}

.app-layout {
  min-height: 100vh;
}


/*=============== table */

.react-resizable {
  position: relative;
  background-clip: padding-box;
}

.react-resizable-handle {
  position: absolute;
  right: -5px;
  bottom: 0;
  z-index: 1;
  width: 10px;
  height: 100%;
  cursor: col-resize;
}


/**/
.ant-card.ant-card-gb {
  background: #f5f9ff;
}

#cy {
  width: 100%;
  height: 450px;
  display: block;
  background: #f5f9ff;
  margin-top: 10px;
}


.factory_tool > * {
  margin: 0 10px 10px 0;
}

.factory_machine_info {
  position: absolute;
    right: 20px;
    bottom: 15px;
    font-size: smaller;
}


/*定義role-user清單大小*/
.role-user-list > .ant-transfer-list {
  height: 400px;
  width: 100%;
}


/*FullScreen 壓縮 header 的動畫效果*/
.ant-layout-header.ant-layout-header--hide {
  height: 0px;
  overflow: hidden;
}

.ant-layout-header {
  transition: all 0.3s ease;
}

/*FullScreen 壓縮 sidebar 的動畫效果*/
.ant-layout-sider.ant-layout-sider--hide {
  width: 0px!important;
  overflow: hidden!important;
  flex: 0 0 0!important;
  min-width: 0!important;
  max-width: 0!important;
}

.ant-layout-sider.ant-layout-sider--hide .ant-menu {
  width: 0;
  height: 0;
}

